body {
  background-color: #d0d0d0;
}

/*******  CHESSBOARD  **********/
.chessboard {
  display: inline-block;
  position: relative;
  width: 656px;
  height: 656px;
  margin: 20px;
  border: 25px solid #333;
}

.square {
  float: left;
  width: 78px;
  height: 78px;
  background-color: #999;
  border: 2px solid black;
  font-size: 50px;
  text-align: center;
  display: table-cell;
  vertical-align: middle;
}

.chessPieceImg {
  width: 60px;
  margin-top: 10px;
  cursor: pointer;
}

.square:nth-child(2n + 1) {
  background-color: black;
}

.square:nth-child(2n + 1) {
  background-color: white;
}

/*********  VALID MOVES ****************/
.validMove {
  /* filter: grayscale(115%); */
  box-shadow: inset 0 0 15px #0f0;
  cursor: pointer;
}

.reverse {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.parent {
  position: relative;
  display: inline-block;
  position: relative;
  text-align: center;
}

/*****  NUMBERS NOTATION ********/
.numParent {
  position: absolute;
  width: 15px;
  height: 652px;

  color: white;
  margin-bottom: 0px;
  right: 25px;
  top: 57px;
}

.num {
  font-family: Arial;
  font-weight: bold;
  height: 62px;
  display: inline-block;
  padding-top: 20px;
  margin-bottom: 0;
}

/******** ALPHABET NOTATION ******/
.alphParent {
  text-align: left;
  position: absolute;
  margin-top: 10px;

  width: 656px;
  height: 15px;
  color: white;

  bottom: 30px;
  left: 45px;
}

.alph {
  font-family: Arial;
  font-weight: bold;
  text-align: center;
  display: inline-block;
  width: 82px;
}

/***********TIME******************/
.parentTime {}

.timeBlack {
  margin-top: 70px;
}

.timeWhite {
  margin-top: 0px;
}

.time {
  width: 200px;
  height: 50px;
  border: 2px solid black;
  border-radius: 3px;
  padding: 10px;
  margin-left: 20px;
}

.timeDetail {
  margin-top: 10px;
}

/*********MOVES HISTORY BOARD**********/
.moveBoard {
  margin-top: 30px;
  margin-bottom: 30px;
  margin-left: 20px;
  height: 400px;
  width: 300px;
  border: 2px solid black;
  border-radius: 8px;
  text-align: left;
  overflow: auto;
}

.history-parent {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 5px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.history {
  font-size: 15px;
  -webkit-flex: 0 1 49%;
          flex: 0 1 49%;
  margin-top: 5px;
  padding: 5px;
  border: 2px solid black;
  /*border-radius: 6px;*/
  box-sizing: border-box;
  text-align: center;
}

.history:hover {
  border: 2px solid green;
  cursor: pointer;
}

.content {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.current {
  border: 2px solid green;
  background-color: green;
}

